import LogoIcon from './logo-icon.vue'

export default {
  name: 'LoadingOverlay',
  components: { LogoIcon },

  data() {
    return {
      isHidden: false,   // 控制背景淡出
      showLogo: true,    // 控制 logo 淡出
      hideText: false,    // 控制文字淡出
      hideBackground: false //背景 class 控制
    }
  },
  mounted() {
    // 1.0s：Logo 與文字 同時開始淡出
    setTimeout(() => {
      this.showLogo = false
      this.hideText = true
    }, 1000)

    // 1.2s：背景開始淡出
    setTimeout(() => {
      this.hideBackground = true
    }, 1600)
  }
}
